/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
    Frame - 
****************************************************************************/
:root {
  --base-font-size: 10px;
}

html,
body {
  width: 100%;
  height: 100%;
  font-size: calc(var(--base-font-size) - 1px);
  @include md {
    font-size: var(--base-font-size);
  }
  @include qhd {
    font-size: calc(var(--base-font-size) + 2px);
  }
  @include uhd {
    font-size: calc(var(--base-font-size) + 10px);
  }
}

#wrap {
  position: relative;
  width: 100%;
  min-width: $G-inner-width;
  font-family: $G-font-family;
  font-size: $G-font-size;
  color: $G-font-color;
  letter-spacing: $G-letter-spacing;

  //  
  &[user-scalable="false"],
  &[user-scalable="false"] ~ * * {
    touch-action: pan-y;
  }
  // @include transition($time: 0.2, $tgs: opacity, $easing: linear);
}

#header {
  position: relative;
  z-index: 30;
  height: $G-header-height;

  // header fixed
  [data-header-fixed="true"] & {
    position: sticky;
    top: 0;
  }
}

#container {
  position: relative;
  z-index: 20;
  background-color: #fff;
}

#footer {
  position: relative;
  z-index: 10;
  height: 13rem;
}

@mixin inner($width: $G-inner-width) {
  position: relative;
  z-index: 1;
  width: $width;
  height: 100%;
  margin: 0 auto;
  padding: 0 $G-grid-gutter;
  @include clearBoth;
}

.inner {
  @include inner;

  &-fluid {
    @include inner($width: 100%);
  }
}
