/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
     (css framework)
***************************************************************************/
/* spacing */
$gaps: ".gap", ".row-gap", ".column-gap";

@each $class in $gaps {
  @for $i from 0 through 100 {
    $gap: $i * 0.1;

    &#{$class + "-" + $i} {
      gap: #{$gap}rem !important;
    }
    &#{$class + "-" + $i} {
      gap: #{$gap}rem 0 !important;
    }
    &#{$class + "-" + $i} {
      gap: 0 #{$gap}rem !important;
    }
  }
}

/*

*/

/*

    margin

    [html  ]
    <div class="mt-10 mb-10"></div>

    [style ]
    <div style="margin-top:1rem;margin-bottom:1rem"></div>

*/
.m {
  $value: t top, r right, b bottom, l left;

  @each $class, $name in $value {
    @for $i from 0 through 300 {
      $margin: $i * 0.1;
      &#{$class + "-" + $i} {
        #{"margin-"+$name}: #{$margin}rem !important;
      }
    }
  }
}

/*

*/

/* color */
.color-primary {
  color: $G-color-primary;
}
.color-secondary {
  color: #2db680;
}
.color {
  $value: #fff, #000, #666, #999, #333, #f86666, #eb2020;

  @each $hexCode in $value {
    $class: str-slice(#{$hexCode}, 2, 7);
    &#{"-" + $class} {
      color: #{$hexCode};
    }
  }
}

/*

*/

/* background-color */
.bg-primary {
  background-color: $G-color-primary !important;
}
.bg-secondary {
  background-color: $G-color-secondary !important;
}
.bg-transparent {
  background-color: transparent !important;
}
.bg {
  $value: #fff, #000, #999, #f2f2f2, #fafafa, #cfcfcf, #dedede;

  @each $hexCode in $value {
    $class: str-slice(#{$hexCode}, 2, 7);
    &#{"-" + $class} {
      background-color: #{$hexCode} !important;
    }
  }
}

/*

*/

/* Visually hidden -         */
.visually-hidden,
.visually-hidden-focusable:not(:focus):not(:focus-within) {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  margin: -1px !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  white-space: nowrap !important;
  border: 0 !important;
}

/*

*/

/* CSS Reset */
.unset-all {
  all: unset !important;
}
.unset {
  $values: padding, margin, font-size;

  @each $value in $values {
    &#{"-" + $value} {
      #{$value}: unset !important;
    }
  }
}

/*

*/

/**
*   clamp ()
*
*   [html  ]
*   <div class="clamp-1"></div>
*
*/

.clamp {
  @for $i from 0 through 10 {
    &#{"-" + $i} {
      @include clamp($i);
    }
  }
}

/*

*/

/*  Scrolling  */
.scroll-container {
  overflow-y: scroll;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

/*

*/

[data-tooltip-text] {
  $triangleW: 1rem;

  position: relative;
  cursor: pointer;
  &::before {
    content: "";
    position: absolute;
    bottom: #{$triangleW * 3};
    left: 1.6rem;
    z-index: 11;
    width: $triangleW;
    height: $triangleW;
    border: 1px solid #666;
    border-top: 0;
    border-right: 0;
    background-color: #fff;
    opacity: 0;
    transform: rotate(-45deg) translate(#{$triangleW / -2}, #{$triangleW / 2});
    transform-origin: 25% 50%;
    @include transition;
  }
  &::after {
    content: attr(data-tooltip-text);
    position: absolute;
    bottom: #{$triangleW * 3};
    left: 0;
    z-index: 10;
    padding: 1rem;
    border: 1px solid #666;
    font-size: $G-font-size;
    font-weight: 700;
    color: $G-font-color;
    line-height: 1.6em;
    word-break: keep-all;
    background: #fff;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    opacity: 0;
    pointer-events: none;
    @include transition;
  }
  &:hover {
    overflow: visible;
    &::before {
      bottom: #{$triangleW * 2};
      opacity: 1;
    }
    &::after {
      bottom: #{$triangleW * 2};
      opacity: 1;
    }
  }
}

