/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
    header
****************************************************************************/
/* header */
#header {
  position: sticky;
  top: 0;
  border-top: 1rem solid $G-color-secondary;
  border-bottom: 1px solid $G-color-secondary;
  background-color: #fff;

  @include lg {
    position: relative;
  }

  /*
  
  */

  /* header-logo */
  .header-logo {
    &__link {
      display: inline-flex;
      align-items: center;
      .logo {
        width: 11.2rem;
        height: auto;
      }
    }
  }

  /*
  
  */

  /* header-today */
  .header-today {
    display: flex;
    align-items: center;
    padding-right: 2.2rem;
    &__title {
      padding-right: 1rem;
      color: $G-color-primary;
    }
    color: $G-default-color;
  }

  /*
  
  */

  /* header-gnb */
  .header-gnb {
  }

  /*
  
  */
}
